/* heebo-100 - hebrew */
@font-face {
  font-family: 'Heebo';
  font-style: normal;
  font-weight: 100;
  src: url('fonts/heebo-v5-hebrew-100.eot'); /* IE9 Compat Modes */
  src: local('Heebo Thin'), local('Heebo-Thin'),
       url('fonts/heebo-v5-hebrew-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('fonts/heebo-v5-hebrew-100.woff2') format('woff2'), /* Super Modern Browsers */
       url('fonts/heebo-v5-hebrew-100.woff') format('woff'), /* Modern Browsers */
       url('fonts/heebo-v5-hebrew-100.ttf') format('truetype'), /* Safari, Android, iOS */
       url('fonts/heebo-v5-hebrew-100.svg#Heebo') format('svg'); /* Legacy iOS */
}
